import { gql } from '@apollo/client/core'

const insertFavoriteCourseMutation = gql`
  mutation InsertFavoriteCourse($course_id: Int, $student_id: uuid) {
    insert_FavoriteCourses(objects: { course_id: $course_id, student_id: $student_id }) {
      returning {
        id
      }
    }
  }
`

export default insertFavoriteCourseMutation
