<template>
  <div class="star-rating-wrap">
    <font-awesome-icon :icon="star" v-for="index of rating" :key="index" class="icon star-solid" />
    <font-awesome-icon :icon="star" v-for="index of 5 - rating" :key="index" class="icon star-empty" />
  </div>
</template>

<script lang="ts">
import { computed, defineComponent } from 'vue'
import { faStar } from '@fortawesome/free-solid-svg-icons'

export default defineComponent({
  props: {
    rating: {
      type: Number,
      required: true
    }
  },
  setup() {
    const star = computed(() => faStar)

    return {
      star
    }
  }
})
</script>

