
import { computed, defineComponent, ref } from 'vue'
import { useMutation, useQuery, useResult } from '@vue/apollo-composable'
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
// graphql
import courseDetailQuery from '@/graphql/queries/courseDetail'
import insertFavoriteCourseMutation from '@/graphql/mutations/insertFavoriteCourse'
import deleteFavoriteCourseMutation from '@/graphql/mutations/deleteFavoriteCourse'
import insertSubscriptionCourseMutation from '@/graphql/mutations/insertSubscriptionCourse'
import {
  CourseDetailQuery,
  DeleteFavoriteCourseMutation,
  InsertFavoriteCourseMutation,
  InsertSubscriptionCourseMutation
} from '@/graphql/@types/types'
//icon
import { faPlayCircle } from '@fortawesome/free-regular-svg-icons'
import { faCheck, faPlus, faExternalLinkAlt, faMedal, faPen } from '@fortawesome/free-solid-svg-icons'
// component
import BackButton from '@/components/BackButton.vue'
import StarRatingView from '@/components/StarRatingView.vue'
import CourseImage from '@/components/CourseImage.vue'
import TeacherInfo from '@/components/TeacherInfo.vue'
import Lectures from '@/components/Lectures.vue'
import SkillTest from '@/components/SkillTest.vue'

export default defineComponent({
  components: {
    BackButton,
    StarRatingView,
    CourseImage,
    TeacherInfo,
    Lectures,
    SkillTest
  },
  setup() {
    const store = useStore()
    const uid = store.state.currentUser?.uid
    const { currentRoute } = useRouter()
    const router = useRouter()

    const { result, onResult, refetch } = useQuery<CourseDetailQuery>(courseDetailQuery, {
      id: currentRoute.value.params.id,
      student_id: uid
    })
    refetch()

    const { mutate: insertFavoriteCourse, onDone: insertFavoriteCourseDone } = useMutation<InsertFavoriteCourseMutation>(
      insertFavoriteCourseMutation,
      () => ({
        variables: {
          course_id: currentRoute.value.params.id,
          student_id: uid
        },
        update: () => {
          refetch()
        }
      })
    )
    const addFavorite = async () => {
      await insertFavoriteCourse()
    }

    const { mutate: deleteFavoriteCourse, onDone: deleteFavoriteCourseDone } = useMutation<DeleteFavoriteCourseMutation>(
      deleteFavoriteCourseMutation,
      () => ({
        variables: {
          course_id: currentRoute.value.params.id,
          student_id: uid
        },
        update: () => {
          refetch()
        }
      })
    )
    const deleteFavorite = async () => {
      await deleteFavoriteCourse()
    }

    const { mutate: insertSubscriptionCourse, onDone: insertSubscriptionCourseDone } = useMutation<InsertSubscriptionCourseMutation>(
      insertSubscriptionCourseMutation,
      () => ({
        variables: {
          course_id: currentRoute.value.params.id,
          student_id: uid
        }
      })
    )

    const addSubscriptionCourse = async () => {
      await insertSubscriptionCourse()
    }

    insertSubscriptionCourseDone(({ data }) => {
      router.push({ name: 'TakeCourseDetail', query: { id: data?.insert_SubscriptionCourses?.returning[0].id } })
    })

    const course = useResult(result, null, (data) => data.Courses[0])
    const lectures = useResult(result, null, (data) => data.Courses[0].Sessions)
    const skillTests = useResult(result, null, (data) => data.Courses[0].Exams)
    const reviews = useResult(result, null, (data) => data.Courses[0].Reviews)
    const subscriptionCourse = useResult(result, null, (data) => data.Courses[0].SubscriptionCourses)

    const courseDetailTransition = () => {
      const id = useResult(result, null, (data) => data.Courses[0].SubscriptionCourses[0].id)
      router.push({ name: 'TakeCourseDetail', query: { id: id.value.toString() } })
    }

    // お気に入り判定処理
    const isFavoriteCourse = ref<undefined | boolean>(undefined)
    const favoriteCourse = useResult(result, null, (data) => data.Courses[0].FavoriteCourses.length)
    isFavoriteCourse.value = !!favoriteCourse.value

    onResult(() => {
      isFavoriteCourse.value = !!favoriteCourse.value
    })

    insertFavoriteCourseDone(() => (isFavoriteCourse.value = true))
    deleteFavoriteCourseDone(() => (isFavoriteCourse.value = false))

    const plusIcon = computed(() => faPlus)
    const checkIcon = computed(() => faCheck)
    const playIcon = computed(() => faPlayCircle)
    const linkIcon = computed(() => faExternalLinkAlt)
    const penIcon = computed(() => faPen)
    const medalIcon = computed(() => faMedal)

    return {
      playIcon,
      plusIcon,
      checkIcon,
      linkIcon,
      medalIcon,
      penIcon,
      course,
      lectures,
      skillTests,
      reviews,
      addFavorite,
      deleteFavorite,
      isFavoriteCourse,
      addSubscriptionCourse,
      subscriptionCourse,
      courseDetailTransition
    }
  }
})
