import { gql } from '@apollo/client/core'

const deleteFavoriteCourseMutation = gql`
  mutation DeleteFavoriteCourse($course_id: Int, $student_id: uuid) {
    delete_FavoriteCourses(where: { course_id: { _eq: $course_id }, student_id: { _eq: $student_id } }) {
      returning {
        id
      }
    }
  }
`

export default deleteFavoriteCourseMutation
