import { gql } from '@apollo/client/core'

const insertSubscriptionCourseMutation = gql`
  mutation InsertSubscriptionCourse($course_id: Int, $student_id: uuid) {
    insert_SubscriptionCourses(objects: { course_id: $course_id, student_id: $student_id }) {
      returning {
        id
      }
    }
  }
`

export default insertSubscriptionCourseMutation
