import { gql } from '@apollo/client/core'

const courseDetailQuery = gql`
  query CourseDetail($id: Int, $student_id: uuid) {
    Courses(where: { id: { _eq: $id } }) {
      image
      detail
      title
      Instructor {
        id
        icon
        name
      }
      Sessions(order_by: { order: asc }) {
        title
      }
      Exams {
        title
      }
      FavoriteCourses(where: { _and: { student_id: { _eq: $student_id } } }) {
        id
      }
      SubscriptionCourses(where: { student_id: { _eq: $student_id }, course_id: { _eq: $id } }) {
        id
      }
      Reviews {
        comment
        score
        Student {
          name
          photoUrl
        }
      }
    }
  }
`
export default courseDetailQuery
